import useInfiniteScroll from 'react-infinite-scroll-hook';
import { Placeholder, Spinner } from '@wordpress/components';
import { withSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';

import Template from './template.js';

import {
	fetchTemplates,
	fetchLibrary,
} from './../data/templates-cloud/index.js';

const TemplatesContent = ( {
	onImport,
	isGeneral,
	items,
	currentPage,
	totalPages,
} ) => {
	const [ isLoading, setLoading ] = useState( false );

	const onLoadMore = async () => {
		if ( currentPage === totalPages ) {
			return;
		}

		setLoading( true );
		if ( isGeneral ) {
			await fetchTemplates( {
				page: currentPage + 1,
				isScroll: true,
			} );
		} else {
			await fetchLibrary( {
				page: currentPage + 1,
				isScroll: true,
			} );
		}
		setLoading( false );
	};

	const infiniteRef = useInfiniteScroll( {
		loading: isLoading,
		hasNextPage: currentPage !== totalPages,
		onLoadMore,
		threshold: 1,
	} );

	return (
		<div
			className="ti-tpc-template-library-templates-container"
			ref={ infiniteRef }
		>
			{ items.map( ( item ) => (
				<Template
					key={ item.template_id }
					id={ item.template_id }
					title={ item.template_name }
					thumbnail={ item.template_thumbnail }
					link={ item.link }
					onImport={ onImport }
				/>
			) ) }

			{ isLoading && (
				<Placeholder>
					<Spinner />
				</Placeholder>
			) }
		</div>
	);
};

export default withSelect( ( select, { isGeneral } ) => {
	const library = isGeneral
		? select( 'tpc/elementor' ).getTemplates()
		: select( 'tpc/elementor' ).getLibrary();

	const { items = [], currentPage, totalPages } = library;

	return { items, currentPage, totalPages };
} )( TemplatesContent );
